@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

:root {
  --primary-color: rgba(13, 110, 139, 0.75);
  --overlay-color: rgba(24, 39, 51 , 0.85);
  --menu-speed: 0.75s;
}

html {
  font-size: 16px;
}

h1 {
  color: #cdcdcd;
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  /* background-color: #8648A3; */
  background-color: #0210249f;
  color: whitesmoke;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.container {
  margin: 0 auto;
  max-width: 90%;
  padding: 5px;
 
}


/* .wrapper {
  display:flex;
  max-width:70%;
  justify-content: flex-end;
} */
.title{
  font-family: 'Happy Monkey', cursive, 'Helvetica Neue',
  sans-serif;
  color: #FFEB11;
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2rem;
}

p {
  font-family: "Quicksand", sans-serif;
  color:  whitesmoke;
  font-size: 1.5rem;
  text-align: left;
}

#navbar {
  position: absolute;
  right: 12px;
  top: -10px;
  margin-bottom: 1rem;
  margin-top: 1rem;
  padding-top: 1rem;
}

.navlink {
  padding: 1rem;
  margin-bottom: 1rem;
 
}

.navlink li {
  display: inline-block;
 
 
}

.navlink a {
  color: whitesmoke;
  padding-left: 2rem;
  font-size: 2rem;
  text-decoration: none;
  cursor: pointer;
  margin-bottom: 1rem;

}
#startShow  {
  font-size: 1.5rem;
  line-height: 1;
  position: relative;
  animation: heading;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  animation-timing-function: ease;
}

@keyframes heading {
  0%   {left: -1000px;}
  25% { left: 300px}
  50% {left: -300px;}
  100% {left: 0px;}
}


.btn {
  display: inline-block;
  border: none;
  background: var(--primary-color);
  color: #fff;
  padding: 0.75rem 1.5rem;
  margin-top: 1rem;
  transition: opacity 0.5s ease-in-out;
  text-decoration: none;
  margin-right:1rem;
}

.btn:hover {
  opacity: 0.8;
  color: #FFEB11;
}


.showlist {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.image {
  width:  300px;
  height: 300px;
  object-fit: cover;
  margin: 0.5rem;
}


@media (max-width: 992px) {
  .showlist {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 600px) {
  .title {
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 0.7rem;
    text-align: center;
    margin-bottom: 1.5rem;
  }
  .showlist {
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
  }

  .image {
    width:  300px;
    height: 300px;
    object-fit: cover;
    margin: 0.3rem;
  }


  .btn {
    display: inline-block;
    border: none;
    background: var(--primary-color);
    color: #fff;
    padding: 0.5rem 0.5rem;
    transition: opacity 0.5s ease-in-out;
    text-decoration: none;
    margin-right:0.5rem;
    margin-bottom:1rem;
  }

  .btn-refresh {
    opacity: 1;
  }

  #navbar {
    position: absolute;
    right: 1px;
    top: -20px;
    margin-bottom: 1rem;
    margin-top: 1rem;
    padding-top: 1rem;
  }
}


.about-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f4f4f423;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.about-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.about-container section {
  margin-bottom: 20px;
}

.about-container section h3 {
  color: #232323;
  margin-bottom: 10px;
}

.about-container section p {
  line-height: 1.6;
}

.about-container div a {
  color: #00C6A7;
  text-decoration: none;
  font-weight: bold;
}

.about-container section a:hover {
  text-decoration: underline;
}

.ring {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login {
  position: sticky;
  width: 300px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}
.login h2 {
  font-size: 2em;
  color: #cdcdcd;
}
.login .inputBx {
  position: relative;
  width: 100%;
}
.login .inputBx input {
  position: relative;
  width: 100%;
  padding: 12px 20px;
  background: transparent;
  border: 2px solid #cdcdcd;
  border-radius: 40px;
  font-size: 1.2em;
  color: #fff;
  box-shadow: none;
  outline: none;
}

.login .inputBx input::placeholder {
  color: rgba(255, 255, 255, 0.75);
}

.login-button {
  position: relative;
  width: 100%;
  padding: 12px 20px;
  background: #cdcdcd;
  border: 2px solid #cdcdcd;
  border-radius: 40px;
  font-size: 1.5em;
  color: #232323;
  box-shadow: none;
  outline: none;
}

.login-button:hover {
  background-color: #00C6A7;
}
